import styled from 'styled-components';
import { Box, Heading } from 'components';
import highlightProduct from 'content/highlight_product.json';
import CardProduct from './components/CardProduct';

const Root = Box.withComponent('section');

const Divider = styled(Box)`
  ${(props) => props.theme.mediaQueries.md} {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 41px;
  }
`;

const CardProductList = styled(Box)`
  display: grid;
  grid-row-gap: ${(props) => props.theme.space.md}px;
  grid-template-columns: minmax(0px, 1fr);

  &:first-child {
    margin-bottom: ${(props) => props.theme.space.md}px;
  }

  ${(props) => props.theme.mediaQueries.lg} {
    &:first-child {
      margin-bottom: 0;
    }
  }
`;

const renderProducts = Object.entries(highlightProduct).map(([product, productDetail]) => (
  <CardProduct
    href="/products"
    name={product}
    description={productDetail.description}
    image={`/picture/product/${productDetail.picture}`}
    bgHover={productDetail.color}
    key={product}
  />
));

const OurPojectSection = ({ ...rest }) => (
  <Root {...rest} pt="lg">
    <Heading color="black" variant="900" mb="md">
      Our Project
    </Heading>
    <Divider>
      <CardProductList>{renderProducts.slice(0, 2)}</CardProductList>
      <CardProductList>{renderProducts.slice(2, 5)}</CardProductList>
    </Divider>
  </Root>
);

export default OurPojectSection;
