import Link from 'next/link';
import styled from 'styled-components';
import PropTypes from 'prop-types';
import { Heading, Paragraph } from 'components';

const CardProductImg = styled.img`
  display: block;
  max-width: 100%;
  margin: ${(props) => props.theme.space.sm}px auto -24px;
  transition: transform 0.2s ease-in-out;
`;

const Card = styled.a`
  position: relative;
  background-color: ${(props) => props.theme.colors.foreground};
  border-radius: 16px;
  cursor: pointer;
  display: block;
  transition: all 0.1s ease-out;
  padding: ${(props) => props.theme.space.md}px ${(props) => props.theme.space.sm}px 0
    ${(props) => props.theme.space.sm}px;

  ${(props) => props.theme.mediaQueries.lg} {
    padding: ${(props) => props.theme.space.md}px ${(props) => props.theme.space.sm}px 0
      ${(props) => props.theme.space.sm}px;
  }

  ${(props) => props.theme.mediaQueries.xl} {
    padding: ${(props) => props.theme.space.lg}px ${(props) => props.theme.space.md}px 0
      ${(props) => props.theme.space.lg}px;
  }

  &:hover {
    background-color: ${(props) => props.bgHover};

    ${Heading}, ${Paragraph} {
      color: #fff;
    }

    ${CardProductImg} {
      transform: scale(1.07);
    }
  }
`;

const StyledParagraph = styled(Paragraph)`
  margin-bottom: ${(props) => props.theme.space.sm}px;

  ${(props) => props.theme.mediaQueries.md} {
    margin-bottom: ${(props) =>
      props.imageSpaceTop ? `${props.imageSpaceTop}px` : `${props.theme.space.sm}px`};
  }
`;

const CardProduct = ({ name, description, image, bgHover, href }) => (
  <Link href={href} passHref>
    <Card bgHover={bgHover}>
      <Heading color="black" variant="600" mb="sm" as="h3">
        {name}
      </Heading>
      <StyledParagraph variant="500" color="grey">
        {description}
      </StyledParagraph>
      <CardProductImg src={image} alt={name} />
    </Card>
  </Link>
);

CardProduct.propTypes = {
  name: PropTypes.string.isRequired,
  description: PropTypes.string.isRequired,
  image: PropTypes.string.isRequired,
  bgHover: PropTypes.string,
  href: PropTypes.string,
};

CardProduct.defaultProps = {
  bgHover: '#2080d9',
  href: '/',
};

export default CardProduct;
