import styled from 'styled-components';
import { Box, Heading, CardBgImage, FloatingBackground } from 'components';
import whatWeDo from 'content/what_we_do.json';

const Root = Box.withComponent('section');

const GridContainer = styled(Box)`
  display: grid;
  grid-gap: 41px;

  ${(props) => props.theme.mediaQueries.lg} {
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 41px;
  }
`;

const WhatWeDoSection = ({ ...rest }) => (
  <Root {...rest} pt="lg">
    <FloatingBackground />
    <Heading color="black" variant="900" mb="md" mt="lg">
      What We Do
    </Heading>
    <GridContainer>
      {whatWeDo.map((item) => (
        <CardBgImage
          bgImage={`/picture/what_we_do/${item.picture}`}
          title={item.title}
          description={item.description}
          key={item.title}
        />
      ))}
    </GridContainer>
  </Root>
);

export default WhatWeDoSection;
